export class ProjectCreatePage {
  viewTitle: string;
  type: number;
  public project = {
    id : '',                    //id
    itemName : '',              //
    itemFounder : '',     //
    founderEmpNum : '003169',         //
    itemLeader : '',            //
    itemLeaderEmpNum :'',
    // empNum: '',
    itemCreateTime : '',        //
    startTime : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),      //
    delayTime : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),      //
    delayDays : '',             //
    itemUpdate : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),     //
    endTime : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),        //
    itemVersion : '',           //
    itemLevel : '',             //
    startResult : '',           //
    endResult : '',             //
    milestone : [],             //
    itemRaise : '',             //
    itemRevision : '',          //
    itemDept : '',              //
    itemState : '',             //
    itemCode : '',              //
    itemWeight : '',            //
    itemProgress : '',          //
    isEnd : false,              //
  };

  constructor(public navCtrl: NavController, public navParams: NavParams, private alertCtrl: AlertController,
              public appService : AppService, public toastCtrl: ToastController) {
    var data = this.navParams.get('project');
    this.type = this.navParams.get('type');
    if (data) {
      this.project = data;
      this.viewTitle = this.project.itemName;
    } else {
      this.viewTitle = '';
    }
  }

  ionViewDidLoad() {

  }

  onPublish() {
    if (this.project.itemName.length < 1) {
      let alert = this.alertCtrl.create({
        title: '',
        subTitle: '!',
        buttons: ['']
      });
      alert.present();
      return;
    }
    if (this.project.milestone.length > 0) {
      var lastMile = this.project.milestone[this.project.milestone.length-1];
      if (lastMile.milestoneLeader.length < 1 || lastMile.milestoneDelivery.length < 1 || lastMile.milestoneSchedule.length < 1 || lastMile.planTime.length < 1) {
        let alert = this.alertCtrl.create({
          title: '',
          subTitle: '!',
          buttons: ['']
        });
        alert.present();
        return;
      }
    }
    this.appService.httpPost("item/create", this.project, this, function (view ,res){
      let toast = view.toastCtrl.create({
        message: '!',
        duration: 3000
      });
      toast.present();
      view.navCtrl.pop();
    } ,true);
  }

  onAddMilestone() {
    if (this.project.milestone.length > 0) {
      var lastMile = this.project.milestone[this.project.milestone.length-1];
      if (lastMile.milestoneLeader == null || lastMile.milestoneDelivery == null || lastMile.milestoneSchedule == null || lastMile.planTime  == null) {
        let alert = this.alertCtrl.create({
            title: '',
            subTitle: '!',
            buttons: ['']
        });
        alert.present();
        return;
      }
      if (lastMile.milestoneLeader.length < 1 || lastMile.milestoneDelivery.length < 1 || lastMile.milestoneSchedule.length < 1 || lastMile.planTime.length < 1) {
        let alert = this.alertCtrl.create({
          title: '',
          subTitle: '!',
          buttons: ['']
        });
        alert.present();
        return;
      }
    }
    var milestone = {
      id : '',                    //id
      milestoneName : ''+(this.project.milestone.length+1),         //
      milestoneLeader : '',       //
      milestoneDelivery : '',     //
      milestoneSchedule : '',     //
      planTime : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),              //
      realTime : "",              //
      remark : '',                //
      isAccomplish : false,       //
      delay : 0,                  //
      subtask : [],              //
    };
    // this.project.milestone.push(milestone);
    this.navCtrl.push(MilestoneDetailPage, {
      milestone : milestone,
      projectname : this.project.itemName,
      pid : this.project.id,
      type : this.type,
      callback : this.milestoneCallback,
    });
  }

  onLookMilestone() {
    this.navCtrl.push(MilestoneDetailPage, {
      project : this.project
    });
  }

  onClickRemoveMilestone($event, mile) {
    if (this.type == 1) {
      this.deleteOneMile(mile)
    } else {
      this.appService.httpDelete("milestone/delete",{"id":mile.id},this,function (view, res) {
        if (res.status == 200) {
            view.deleteOneMile(mile);
        }
      },true);
    }
  }

  deleteOneMile(mile) {
      var deleteId = mile.id;
      var index = -1;
      for (let i=0; i<this.project.milestone.length; i++) {
          var milestone1 = this.project.milestone[i];
          if (deleteId == milestone1.id) {
              index = i;
              break;
          }
      }
      if (index >= 0) {
          this.project.milestone.splice(index, 1);
          for (let i=0; i<this.project.milestone.length; i++) {
              var milestone2 = this.project.milestone[i];
              milestone2.milestoneName = ''+(i+1);
          }
      }
  }

  onEndDirector($event) {
    this.navCtrl.push(ContactPage, {
        callback: this.directorCallback,
        type: 1,
    });
  }

  milestoneCallback = (milestone) =>
  {
    return new Promise((resolve, reject) => {
      if (typeof (milestone) != 'undefined') {
        var isIn = false;
        for (let i=0; i<this.project.milestone.length; i++) {
          var tempMile = this.project.milestone[i];
          if (tempMile.id == milestone.id) {
            isIn = true;
            this.project.milestone.splice(i, 1, milestone);
            break;
          }
        }
        if (!isIn) {
          this.project.milestone.push(milestone);
        }
      } else {

      }
      resolve();
    });
  };

  directorCallback = (director) =>
  {
    return new Promise((resolve, reject) => {
        if (typeof (director) != 'undefined') {
          this.project.itemLeader = director.name;
          this.project.itemLeaderEmpNum = director.username;
        } else {

        }
        resolve();
    });
  }
}



