import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { JsonpModule } from "@angular/http";
import { Keyboard } from "@ionic-native/keyboard";
import { HttpModule } from "@angular/http";

//
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ProjectCreatePage } from "../pages/project-create/project-create";
import { MilestoneDetailPage } from "../pages/milestone-detail/milestone-detail";
import { ProjectDetailPage, PopoverPage } from "../pages/project-detail/project-detail";
import { SubtaskPage } from "../pages/subtask/subtask";
import { SearchPage } from "../pages/search/search";
import { ContactPage } from "../pages/contact/contact";

//
import { ComponentsModule } from "../components/components.module";

//pipe
import { PipesModule } from "../pipes/pipes.module";
import { AppService } from "./app.service";

//

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ProjectCreatePage,
    MilestoneDetailPage,
    ProjectDetailPage,
    SubtaskPage,
    SearchPage,
    ContactPage,

    PopoverPage,
  ],
  imports: [
    BrowserModule,
    ComponentsModule,
    PipesModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {
      backButtonText: '',
      mode: 'ios',
      tabsHideOnSubPages: true,
      modalEnter: 'modal-slide-in',
      modalLeave: 'model-slide-out',
      tabsPlacement: 'bottom', //tabweizhi
      pageTransition: 'ios-transition' //ios
    }, 
{
  links: [
    { loadChildren: '../pages/contact/contact.module#ContactPageModule', name: 'ContactPage', segment: 'contact', priority: 'low', defaultHistory: [] }
  ]
}),
    JsonpModule,
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ProjectCreatePage,
    MilestoneDetailPage,
    ProjectDetailPage,
    SubtaskPage,
    SearchPage,
    ContactPage,

    PopoverPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Keyboard,
    AppService,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



